import React from "react";
import {
  FaFacebook,
  FaInstagram,
  FaTwitter,
  FaLinkedin,
  FaYoutube,
  FaPinterest
} from "react-icons/fa";
import { Container } from "react-bootstrap";
function Footer() {
  const year = new Date().getFullYear();
  return (
    <footer>
      <Container className="text-center bg-secondary p-5">
        
        <div className="d-flex justify-content-around fs-3 text-danger">
          <FaFacebook /> <FaInstagram /> <FaTwitter /> <FaLinkedin />{" "}
          <FaYoutube /> <FaPinterest />
        </div>
        <div className="pb-5">
          Create by <a href="https://ourz.tech">Ourz</a>
          <div>
            Copyright &copy; {year} All rights reserved by{" "}
            <a href="https://emindcafe.org">e-&nbsp;Mind Cafe</a>.
          </div>
        </div>
      </Container>
    </footer>
  );
}

export default Footer;
