import React, { useState } from "react";
import { Navbar, Nav, Container } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.css";
import logo from "../images/logo.png";
function Header() {
  const [isScroll, setIsScroll] = useState(false);
  const hanndleScroll = () => {
    if (window.scrollY >= 100) {
      setIsScroll(true);
    } else {
      setIsScroll(false);
    }
  };
  window.addEventListener("scroll", hanndleScroll);

  function handleClick(event) {
    // event.preventDefault();
    // console.log("clicked.");
  }

  return (
    <header className="container bg-secondary">
      <Container>
        <Nav
          className="d-flex justify-content-end fs-5"
        >
          <Nav.Link href="#home" onClick={handleClick}>
            Home
          </Nav.Link>
          <Nav.Link href="#about" onClick={handleClick}>
            About
          </Nav.Link>
          <Nav.Link href="#education" onClick={handleClick}>
            Education
          </Nav.Link>
        </Nav>
      </Container>
      <Container
        className={isScroll ? "display-flex" : "d-flex justify-content-between"}
      >
        <Navbar.Brand href="/">
          <img src={logo} width={200} alt="E-mind Cafe Logo" />
        </Navbar.Brand>
        <a className="pt-4" href="tel:+91-9923852939">
          +91-9923852939
        </a>
      </Container>
      <Container>
        <Navbar
          className="navbar fs-4"
          collapseOnSelect
          expand="xl"
          variant={isScroll ? "dark" : "light"}
          fixed="sticky"
        >
          <Navbar.Toggle aria-controls="responsive-navbar-nav" />
          <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="d-flex justify-content-end">
              <Nav.Link href="#home" onClick={handleClick}>
                Home
              </Nav.Link>
              <Nav.Link href="#about" onClick={handleClick}>
                About
              </Nav.Link>
              <Nav.Link href="#education" onClick={handleClick}>
                Education
              </Nav.Link>
              <Nav.Link href="#experience" onClick={handleClick}>
                Experience
              </Nav.Link>
              <Nav.Link href="#skill" onClick={handleClick}>
                Skills
              </Nav.Link>
              <Nav.Link href="#project" onClick={handleClick}>
                Projects
              </Nav.Link>
              <Nav.Link href="#contact" onClick={handleClick}>
                Contact
              </Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Navbar>
      </Container>
    </header>
  );
}

export default Header;
